import React, { useState, useEffect } from "react";
import axios from "axios";
import Menu from "../../../../Components/Menu/menu";
import "./P4.css";

function Index() {
  const [data, setData] = useState([]); //  API
  const [searchTerm, setSearchTerm] = useState(""); // 
  const [loading, setLoading] = useState(true); // 
    const currentYear = new Date().getFullYear(); // 
    const [yearFilter, setYearFilter] = useState(currentYear.toString()); 

  //  API  Component  Mount
  useEffect(() => {
    axios
      .get(process.env.REACT_APP_API_KEY + "/api/policypayment") //  URL  API 
      .then((response) => {
        setData(response.data); //  State
        setLoading(false); // 
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
        setLoading(false); // 
      });
  }, []);

  // 
  const filteredData = data
    .filter((item) => {
      const itemYear = new Date(item.date).getFullYear().toString(); //  date
      const matchesSearch =
        item.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        item.date.includes(searchTerm);
      const matchesYear = yearFilter === "" || itemYear === yearFilter; // 
      return matchesSearch && matchesYear;
    })
    .sort((a, b) => new Date(b.date) - new Date(a.date)); // 

  return (
    <div>
      <Menu />

      {/* Hero Section */}
      <div
        className="container-fluid py-5 sticky-service"
        style={{
          backgroundImage: `url(${process.env.PUBLIC_URL}/assest/img/14.png)`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          height: "45vh",
        }}
      >
        <div className="container py-5">
          <div className="text-center mx-auto pb-5" style={{ maxWidth: 800 }}>
            <h1
              className="display-3 text-capitalize mb-3"
              style={{ color: "white", marginTop: "60px" }}
            >
              Dividend Policy and Payment
            </h1>
          </div>
        </div>
      </div>

      {/* Content Section */}
      <div
        className="container-fluid py-5"
        style={{
          backgroundColor: "#ffffff",
          boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)",
        }}
      >
        <div className="container">
          {/*  */}
          <div className="policy-info mb-4">
            <h3>TRU's Dividend Policy and History</h3>
            <p>
            Dividend Payment Policy of the Company<br />
            The company has a dividend payment policy. At the rate of approximately 40% of the net profit after tax each year of the consolidated financial statements.
It will be paid in the following year. However, the consideration of such dividend payment will depend on the cash flow and investment burden of the Company and its affiliates.
including other necessary and appropriate in the future.
            </p>
          </div>

          <div className="search-section mb-4 d-flex justify-content-between">
            {/*  */}
            <input
              type="text"
              placeholder=""
              className="form-control"
              style={{ width: "300px", marginRight: "10px" }}
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />

            {/*  */}
            <select
              className="form-select"
              style={{ width: "150px" }}
              value={yearFilter}
              onChange={(e) => setYearFilter(e.target.value)}
            >
              <option value="" disabled>
              Select Year
              </option>
              <option value={currentYear}>{currentYear}</option>
              <option value={currentYear - 1}>{currentYear - 1}</option>
              <option value={currentYear - 2}>{currentYear - 2}</option>
              <option value={currentYear - 3}>{currentYear - 3}</option>
            </select>
          </div>

          {/*  */}
          {loading && (
            <div className="text-center py-5">
              <p>Loading data...</p>
            </div>
          )}

          {/*  */}
          {!loading && (
            <div className="data-list">
              {filteredData.map((item, index) => {
                //  pdf_url  URL 
                const pdfUrl = item.pdf_url.startsWith("http")
                  ? item.pdf_url
                  : `${process.env.REACT_APP_PDF_KEY}/uploads/pdf_files/${item.pdf_url_en}`;

                return (
                  <div
                    key={index}
                    className="data-item d-flex justify-content-between align-items-center"
                    onClick={() =>
                      window.open(pdfUrl, "_blank", "noopener noreferrer")
                    }
                    style={{
                      cursor: "pointer",
                      padding: "10px",
                      borderRadius: "10px",
                      border: "1px solid #ddd",
                      marginBottom: "10px",
                      backgroundColor: "#f9f9f9",
                    }}
                  >
                    <div>
                      <span className="date text-primary">{item.date}</span>
                      <p className="mb-0">{item.title_en}</p>
                    </div>
                    <button className="btn btn-outline-primary">
                      <i className="fas fa-arrow-right"></i>
                    </button>
                  </div>
                );
              })}

              {/*  */}
              {filteredData.length === 0 && (
                <div className="text-center py-3">
                  <p>No data was found that matches your search.</p>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Index;
